body {
  margin: 0;
  padding: 0;
  height: 100vh;
  background: rgba(179, 220, 237, 1);
  background: -moz-linear-gradient(top, rgba(179, 220, 237, 1) 0%, rgba(0, 116, 158, 1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(179, 220, 237, 1)), color-stop(100%, rgba(0, 116, 158, 1)));
  background: -webkit-linear-gradient(top, rgba(179, 220, 237, 1) 0%, rgba(0, 116, 158, 1) 100%);
  background: -o-linear-gradient(top, rgba(179, 220, 237, 1) 0%, rgba(0, 116, 158, 1) 100%);
  background: -ms-linear-gradient(top, rgba(179, 220, 237, 1) 0%, rgba(0, 116, 158, 1) 100%);
  background: linear-gradient(to bottom, rgba(179, 220, 237, 1) 0%, rgba(0, 116, 158, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#b3dced', endColorstr='#00749e', GradientType=0);
  color: #ffffff;

}

#root {
  color: #ffffff;
  text-shadow: 1px 1px 2px black, 0 0 1em #000000;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
form input{
  width: 100%;
  margin-bottom: 20px;
  background: transparent;
  border: none;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
}
form input::placeholder{
color:#ffffff;
}
form input:active, form input:focus{
  background: transparent;
}
form button{
  background: #1471eb;
  color: #ffffff;
  border: none;
  border-radius: 15px;
  padding: 15px;
  text-align: center;
  margin: 0 auto;
  display: block;
}
.wrapper{
  margin: 0 auto;
  background: rgba(0, 188, 212, .3);
  width: 100%;
  border: 1px solid;
  padding: 20px;
}
.icon-icon {
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
